import React, {Component} from 'react'
import { Link } from 'gatsby'
import Helmet from 'react-helmet'
import PropTypes from 'prop-types'
import config from '../../../config'

import Layout from '../Layout'
import { ContactForm } from "../forms/ContactForm"
import Carousel from '../Carousel'
import Showcase from '../Showcase'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faLink, faFileInvoiceDollar, faShieldAlt, faChartLine, faCheckCircle, faArrowCircleRight, faChevronLeft, faChevronRight } from '@fortawesome/free-solid-svg-icons'

library.add(faLink, faFileInvoiceDollar, faShieldAlt, faChartLine, faCheckCircle, faArrowCircleRight, faChevronLeft, faChevronRight)

const HomePageTemplate = ({
  title,
  heading,
  description,
  offerings,
  meta_title,
  meta_description,
  usecases,
  carousel,
  datafeed,
  consumerfeatures,
  enterprisefeatures,
  features,
  cta
}) => (
  <div>
    <Helmet>
      <title>{meta_title}</title>
      <meta name='description' content={meta_description} />
    </Helmet>
    <div
      className="bg-indigo-800 bg-cover bg-top bg-no-repeat flex flex-col font-sans"
      style={{backgroundImage: `linear-gradient(to bottom, rgba(255,255,255,0) 30%, rgba(0,0,0,1) 90%), url("/img/earth.jpg")`, marginTop:'-80px'}}>
      <div className="container mx-auto px-8">
        {/* hero */}
        <main className="flex flex-wrap items-center text-center my-12 pt-48 lg:px-24 text-white">
          <div className="w-full md:w-full whitespace-pre-line">
            <h1 className="text-6xl tracking-tight font-light">
              {heading}
            </h1>
            <h4 className="text-base font-light text-gray-500 my-4">
              {description}
            </h4>
            <form className="w-full my-24">
              <div className="flex items-center border-b border-b-2 border-teal-500 py-2 mx-24">
                <input className="appearance-none bg-transparent border-none w-full text-gray-200 mr-3 py-1 px-2 leading-tight focus:outline-none" type="text" placeholder="Full Name" aria-label="Full name" />
                <input className="appearance-none bg-transparent border-none w-full text-gray-200 mr-3 py-1 px-2 leading-tight focus:outline-none" type="text" placeholder="Email Address" aria-label="Email Address" />
                <button className="flex-shrink-0 bg-teal-500 hover:bg-teal-700 border-teal-500 hover:border-teal-700 text-sm border-4 text-white py-1 px-8 rounded" type="button">
                  Inquire
                </button>
              </div>
            </form>
          </div>
        </main>

        <hr className="border-gray-700" />

        {/* subhero section */}
        <div className="flex flex-wrap overflow-hidden text-center pb-12 my-12 items-center text-gray-500">
          {usecases.map(u => (
            <div key={`feature_${u.heading}`} className="w-1/4 whitespace-pre-line">
              <FontAwesomeIcon icon={u.icon} className="text-4xl mb-4 text-teal-500" />
              <h2 className="text-lg font-base pb-2 text-gray-100">
                {u.heading}
              </h2>
              <h3 className="text-base font-light">
                {u.description}
              </h3>
            </div>
          ))}
        </div>
      </div>
    </div>

    {/* first section: tag types carousel */}
    <div className="bg-white flex flex-col"
      style={{backgroundImage: `linear-gradient(to bottom, rgba(255,255,255,1) 50%, #f2f2f2 100%), url("/img/earth.jpg")`}}>
      <div className="container mx-auto px-8">
        <Carousel data={carousel} />
      </div>
    </div>

    <div className="bg-white flex flex-col">
      <div className="container mx-auto px-8">

        {/* second section: example use cases */}
        <div className="flex flex-wrap overflow-hidden text-left py-12 my-12 items-center">
          <div className="w-full text-center text-4xl">Data Feed</div>
          <Showcase data={datafeed} />
        </div>
      </div>
    </div>
    <div className="bg-blue-900 flex flex-col">
      <div className="container mx-auto px-8">

        {/* third section: consumer simplicity */}
        <div className="flex flex-wrap overflow-hidden text-left pt-16 my-12 items-center">
          <div className="w-full text-3xl font-light text-gray-100 text-center mb-16">Consumer Simplicity</div>

          {/* Left */}
          <div className="w-1/3 text-right pl-24 pr-12 text-gray-100">
            {consumerfeatures.slice(0, consumerfeatures.length/2).map((f, i) => (
              <div key={`consumer_${i}`} className="my-8">
                <FontAwesomeIcon icon={f.icon} className="text-3xl mb-4 text-teal-500" />
                <h3 className="text-base font-base">{f.heading}</h3>
                <p className="text-sm">{f.description}</p>
              </div>
            ))}
          </div>

          {/* Center */}
          <div className="w-1/3">
            <img src="/img/mobile_screen.png" />
          </div>

          {/* Right */}
          <div className="w-1/3 text-left pl-12 pr-24 text-gray-100">
            {consumerfeatures.slice(consumerfeatures.length/2, consumerfeatures.length).map((f, i) => (
              <div key={`consumer_${3+i}`} className="my-8">
                <FontAwesomeIcon icon={f.icon} className="text-3xl mb-4 text-teal-500" />
                <h3 className="text-base font-base">{f.heading}</h3>
                <p className="text-sm">{f.description}</p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
    <div className="bg-white flex flex-col">
      <div className="container mx-auto px-8">

        {/* fourth section: enterprise efficiency */}
        <div className="flex flex-wrap overflow-hidden text-left py-12 my-12 items-center">
          <div className="w-full text-3xl font-light text-gray-800 text-center mb-16">Enterprise Efficiency</div>
          {/* Left */}
          <div className="w-1/2">
            <img className="" src="/img/laptop.png" />
          </div>
          {/* Right */}
          <div className="w-1/2 pl-20 pr-24">
            <ul>
              {enterprisefeatures.map((f, i) => (
                <li key={`enterprise_${i}`} className="mb-12">
                  <h3 className="text-lg font-medium leading-loose">{f.heading}</h3>
                  <p className="text-base font-light">{f.description}</p>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div className="bg-gray-100 flex flex-col">
      <div className="container mx-auto px-8">

        {/* fifth section: features */}
        <div className="flex flex-wrap overflow-hidden text-left py-12 my-12 items-center">
          <div className="w-full text-center text-4xl font-light my-6">Features</div>
          {features.map((f, i) => (
            <div key={`feature_${i}`} className="flex w-1/4 p-4 text-gray-800">
              <FontAwesomeIcon icon={faCheckCircle} className="flex-shrink text-3xl mx-4 text-teal-500" />
              <div className="flex-grow">
                <h3 className="text-base font-base">{f.heading}</h3>
                <p className="font-light text-sm">{f.description}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
    <div className="bg-white flex flex-col">
      <div className="container mx-auto px-8">

        {/* cta */}
        <div className="flex flex-wrap overflow-hidden text-center items-center">
          {cta.map((c, i) => (
            <div key={c.heading} className="w-1/2 px-32 py-24  transition ease-out duration-700 transform hover:scale-105">
              (screenshot){c.screenshot}
              <br />
              <br />
              <br />
              <h2 className="text-xl">{c.heading}</h2>
              <p className="text-sm my-4">{c.description}</p>
              <a href={c.link}>
                <button className="my-4 flex-shrink-0 bg-teal-500 hover:bg-teal-700 border-teal-500 hover:border-teal-700 text-sm border-4 text-white py-1 px-8 rounded" type="button">
                  {c.linklabel}
                  <FontAwesomeIcon icon={faArrowCircleRight} className="ml-2" />
                </button>
              </a>
            </div>
          ))}
        </div>
      </div>
    </div>

    {/* get in touch */}
    <div className="bg-blue-900 flex flex-col text-white">
      <div className="container mx-auto px-8">
        <div className="w-full text-center text-4xl font-light my-6">Get in Touch</div>
        <div className="w-full text-center text-xl font-light my-6 whitespace-pre-line">
          <a href={`mailto:${config.email}`}>{config.email}</a>{`\n`}
          <a href={`tel:+${config.phone.replace(/\D/g,'')}`}>{config.phone}</a>
        </div>
        <div className="flex flex-wrap overflow-hidden text-left py-12 my-12 items-stretch">
          <div className="w-1/2 px-8 flex flex-1">
            <iframe className="border-none w-full h-full" frameBorder="0" src={`https://www.google.com/maps/embed/v1/place?q=place_id:ChIJ37HL3ry3t4kRv3YLbdhpWXE&key=${config.googleCloudApiKey}`} allowFullScreen />
          </div>
          <div className="w-1/2 px-8">
            <ContactForm />
          </div>
        </div>
      </div>
    </div>
  </div>
)

HomePageTemplate.propTypes = {
  title: PropTypes.string,
  meta_title: PropTypes.string,
  meta_description: PropTypes.string,
  heading: PropTypes.string,
  description: PropTypes.string,
  usecases: PropTypes.array,
  features: PropTypes.array,
  carousel: PropTypes.array,
  datafeed: PropTypes.array,
  consumerfeatures: PropTypes.array,
  enterprisefeatures: PropTypes.array,
  cta: PropTypes.array
}

export default HomePageTemplate
